import React from "react";

class OptInForm extends React.Component {
  state = {
    checked: false,
  };

  timeout = null;

  toggleSwitch = () => {
    this.setState({
      checked: !this.state.checked,
    });
    clearTimeout(this.timeout);
    document.getElementById(`opt-in-${this.props.NCTId}`).disabled = true;

    this.timeout = setTimeout(() => {
      document.getElementById(`opt-in-${this.props.NCTId}`).disabled = false;
    }, 250);
  };

  render() {
    return (
      <form className="border p-3">
        <div className="form-check">
          <input
            onChange={this.toggleSwitch}
            type="checkbox"
            className="form-check-input"
            value=""
            id={`opt-in-${this.props.NCTId}`}
            data-toggle="collapse"
            data-target={`#form-${this.props.NCTId}`}
          />
          <label
            htmlFor={`opt-in-${this.props.NCTId}`}
            className="form-check-label"
          >
            I have read the information above and acknowledge that I fulfill all
            eligibility criteria to the best of my knowledge. I agree to be
            contacted by the study team for further screening and participation
            in this study.
          </label>
        </div>
        <div id={`form-${this.props.NCTId}`} className="collapse text-center">
          <input
            required
            type="text"
            className="form-control form-control mt-3"
            placeholder="Name"
            name="name"
          />
          <input
            required
            type="email"
            className="form-control form-control mt-3"
            placeholder="Email"
            name="email"
          />
          <input
            required
            type="tel"
            className="form-control form-control mt-3"
            placeholder="Contact No."
            name="contactNo"
          />
          <textarea
            className="form-control"
            placeholder="Remarks"
            rows="3"
          ></textarea>
          <hr />
          {this.props.recipientList.map((item, index) => {
            return (
              <div key={index}>
                <h6 className="font-weight-bold text-left">
                  Recipient {index + 1}:
                </h6>
                <div className="input-group">
                  <input
                    type="text"
                    className="form-control"
                    value={item[0]}
                    disabled
                  />
                </div>
                <div className="input-group">
                  <input
                    type="text"
                    className="form-control"
                    value={item[1]}
                    disabled
                  />
                </div>
                <br />
              </div>
            );
          })}
          <button type="button" className="btn btn-primary">
            Send to Study Team
          </button>
        </div>
      </form>
    );
  }
}

export default OptInForm;
