import React from "react";
import PropTypes from "prop-types";
import FavButton from "./FavButton.js";
import SlideIn from "./SlideIn.js";
import ButtonStatus from "./ButtonStatus.js";

const Card = ({ study, index }) => {
  const NCTId = study.ProtocolSection.IdentificationModule.NCTId;
  const protocolTitle =
    study.ProtocolSection.IdentificationModule.OfficialTitle;
  const studyType = study.ProtocolSection.DesignModule.StudyType;
  const healthyVolunteer =
    "HealthyVolunteers" in study.ProtocolSection.EligibilityModule
    ? study.ProtocolSection.EligibilityModule.HealthyVolunteers
    : "No";
  let conditions =
    study.ProtocolSection.ConditionsModule.ConditionList.Condition;
  if (conditions.length > 3) {
    conditions = conditions.slice(0, 2);
    conditions.push("...");
  }
  const enrollmentCount =
    "EnrollmentInfo" in study.ProtocolSection.DesignModule
      ? study.ProtocolSection.DesignModule.EnrollmentInfo.EnrollmentCount
      : "Not Specified";
  let locations =
    "ContactsLocationsModule" in study.ProtocolSection &&
    "LocationList" in study.ProtocolSection.ContactsLocationsModule
      ? study.ProtocolSection.ContactsLocationsModule.LocationList.Location
      : "No Data";
  if (locations !== "No Data" && locations.length > 3) {
    locations = locations.slice(0, 2);
    locations.push({ LocationFacility: "..." });
  }

  let status = study.ProtocolSection.StatusModule.OverallStatus;
  status =
    status === "Recruiting"
      ? "Recruiting"
      : status === "Completed"
      ? "Completed"
      : "Not Recruiting";

  const handleFavClick = (favourite, NCTId) => {
    if (favourite) {
      let fav = JSON.parse(localStorage.getItem("favourite")) || [];
      if (fav.indexOf(NCTId) < 0) fav.push(NCTId);
      localStorage.setItem("favourite", JSON.stringify(fav));
    } else {
      let fav = JSON.parse(localStorage.getItem("favourite")) || [];
      if (fav.indexOf(NCTId) >= 0) fav.splice(fav.indexOf(NCTId), 1);
      localStorage.setItem("favourite", JSON.stringify(fav));
    }
  };

  const openSlideIn = () => {
    document.getElementById(`slide-in-${NCTId}`).click();
    window.history.pushState({ slideIn: {NCTId} }, ""); // write twice for it to work
    window.history.pushState({ slideIn: {NCTId} }, "");
    window.addEventListener('popstate', closeSlideIn, false);
  }
  const closeSlideIn = (event) => {
    if (event.state) {
      document.getElementById(`close-slide-in-${NCTId}`).click();
      window.removeEventListener('popstate', closeSlideIn, false)
    }
  }


  return (
    <tr className="table-card" key={NCTId}>
      <td className="table-serial">
        <span>{index + 1}.</span>
      </td>
      <td className="table-content">
        <span>
          <a data-toggle="modal" onClick={openSlideIn} href={`#slide-in-${NCTId}`}>
            {protocolTitle}
          </a>
        </span>
        <br />
        <div className="table-content-info">
          <table className="table table-borderless table-sm">
            <tbody>
              <tr>
                <td>Study Type:</td>
                <td>{studyType}</td>
              </tr>
              <tr>
                <td>
                  {conditions.length > 1
                    ? "Target Conditions:"
                    : "Target Condition:"}
                </td>
                <td>
                  {conditions.map((condition, index) => (
                    <p key={index}>{condition}</p>
                  ))}
                </td>
              </tr>
              <tr>
                <td>Healthy Volunteers:</td>
                <td>{healthyVolunteer === "No" ? "No" : "Yes"}</td>
              </tr>
              <tr>
                <td>Target Enrolment:</td>
                <td>
                  {enrollmentCount !== "Not Specified"
                    ? `${enrollmentCount} participants`
                    : `${enrollmentCount}`}
                </td>
              </tr>
              <tr>
                <td>{locations.length > 1 ? "Locations:" : "Location:"}</td>
                <td>
                  {locations !== "No Data" ? (
                    locations.map((location, index) => (
                      <p key={index}>
                        {location.LocationFacility}
                        {location.LocationFacility !== "..." && ","}{" "}
                        {location.LocationCountry}
                      </p>
                    ))
                  ) : (
                    <p>No Data</p>
                  )}
                </td>
              </tr>
            </tbody>
          </table>
        </div>
        {/* bottom row */}
        <div className="table-content-more">
          <FavButton handleClick={handleFavClick} NCTId={NCTId} />
          <ButtonStatus status={status} />
        </div>
      <SlideIn study={study} />
      </td>
    </tr>
  );
};
Card.propTypes = {
  study: PropTypes.object.isRequired,
  index: PropTypes.number.isRequired,
};

export default Card;
