import React from "react";
import { autosuggest_base_api } from "../../others/configuration.js";
import { autocomplete } from "../../others/common_methods/autocomplete.js";

class TextFilters extends React.Component {
  state = {
    value: "",
  };

  timeout = null;

  updateState = () => {
    let value = document.getElementById(this.props.id).value;
    this.setState({
      value,
    });
    clearTimeout(this.timeout);

    this.timeout = setTimeout(() => {
      clearTimeout(this.searchItem);
      this.props.handleChange(this.state.value);
    }, 50);
  };

  doSearch = (event) => {
    this.setState({
      value: event.target.value,
    });
    clearTimeout(this.timeout);

    this.timeout = setTimeout(() => {
      clearTimeout(this.searchItem);

      if (this.state.value.length > 0) {
        let full_url = autosuggest_base_api + this.state.value;
        fetch(full_url)
          .then((res) => res.json())
          .then((res) => {
            let result = res[3].reduce((arr, elem) => arr.concat(elem[0]), []); // get list of suggestions
            autocomplete(
              document.getElementById(this.props.id),
              result,
              this.updateState
            );
          });
      }

      this.searchItem = setTimeout(() => {
        this.props.handleChange(this.state.value);
      }, 750);
    }, 50);
  };

  render() {
    return (
      <input
        className="form-control"
        id={this.props.id}
        type={this.props.type}
        placeholder={this.props.placeholder}
        onChange={this.doSearch}
        value={this.state.value}
        autocomplete="off"
      />
    );
  }
}

export default TextFilters;
